                        'autoCloseOn' : true,
                        'autoOpenOn' : true,
                        'content' : context,
                        'custom' : true,
                        'dy': -90 * this.map.getZoom(),
            };
            this.filterWindow = new maptalks.ui.InfoWindow(filterBeacons);
