import axios from 'axios'
import { fail } from 'assert';

export const updateIpcamData = (ipcam, successCallback, failCallback) => {
  axios
  .post(`${ window.CONSTANTS.URL.CONSOLE }/dash/cam/location`, {'cam': ipcam}, {
      headers: {
        'Content-Type': 'application/json'
      }
  })
  .then(e => {
    console.debug(`Sucess to post cam location in id ${ipcam.id}`);
    successCallback();
  })
  .catch(e => {
    failCallback();
  }); 
}

