export const getInfo = (readyCallback) => {
  axios({
    url: `${ window.CONSTANTS.URL.CONSOLE }/dash/location/info`,
    method: 'GET',
    responseType: 'text' // important
  }).then(response => {
    if (!!response.data) {
      readyCallback(response.data);
    } else {
      console.warn('File is not exist')
      readyCallback();
    }
  }).catch(error => {
    console.warn("Failed to get info ", error);
    readyCallback();
  });
}

const _getIpcams = (readyCallback) => {
  axios({
    url: `${ window.CONSTANTS.URL.CONSOLE }/dash/cam/list/ipcam`,
    method: 'GET',
    responseType: 'text' // important
  }).then(response => {
    if(!!response.data) {
      if (response.data >= 30) {
        _getIpcams(readyCallback);
      }
      readyCallback(response.data);
    } else {
      console.warn('Ipcam does not exist');
      readyCallback([]);
    }
  }).catch(error => {
    console.warn("Failed to get ipCams", error);
    readyCallback([]);
  }) 
}

export const getIpcams = (readyCallback) => {
  _getIpcams(readyCallback);
}

