const _getDetectBeaconList = (hubId, queryId, successCallback, failCallback) => {
  var context = '';
  if (!!queryId) {
    context = `/${ queryId }`;
  }
  axios({
    url: `${ window.CONSTANTS.URL.CONSOLE }/dash/beacons/detected/${ hubId }${ context }`,
    method: 'GET',
    responseType: 'text' // important
  }).then(response => {
    const dataObj = response.data;
    if (!!dataObj && _.isArray(dataObj.data)) {
      if (dataObj.data.length >= 30) {
        _getDetectBeaconList(hubId, dataObj.query_id, successCallback, failCallback);
      }
      successCallback(dataObj.data);
    } else {
      failCallback('not exist');
    }
  }).catch(error => {
    failCallback(error);
  });
}

export const getDetectBeaconList = (hubId, successCallback, failCallback) => {
  _getDetectBeaconList(hubId, '', successCallback, failCallback);
}

